import React from 'react'

import { 
    ModalBackground, 
    ModalContainer } from './Modal.element';

const Modal = ({showModal, childComp}) => {
    return (
        <>
        {showModal ? (
            <ModalBackground>
                <ModalContainer>
                    {childComp}
                </ModalContainer>
            </ModalBackground>
        ) : null}
        </>

    )
}

export default Modal
